		>
			<v-icon v-if="!drawer"> mdi-text </v-icon>

			<v-icon v-else>
				<!-- mdi-dots-vertical -->
				mdi-menu
			</v-icon>
		</v-btn>

		<v-toolbar-title
			class="hidden-sm-and-down font-weight-light"
			v-text="$route.name"
		/>

		<v-spacer />

		<!-- <v-text-field
			:label="$t('search')"
			color="secondary"
			hide-details
			style="max-width: 165px"
		>
			<template v-if="$vuetify.breakpoint.mdAndUp" v-slot:append-outer>
				<v-btn class="mt-n2" color="#f8f9fa" elevation="0" fab small>
					<v-icon>mdi-magnify</v-icon>
				</v-btn>
			</template>
