		<div class="mx-3" />

		<v-btn class="ml-1" min-width="0" text to="/">
			<v-icon size="23">mdi-account</v-icon>
		</v-btn>

		<!-- <v-btn class="ml-1" min-width="0" text to="/Dashboard">
			<v-icon>mdi-view-dashboard</v-icon>
		</v-btn> -->

		<v-btn
			class="ml-1 mr-0"
			elevation="0"
			min-width="0"
			href="https://github.com/HAR00N"
			target="_blank"
			color="#f8f9fa"			
		>
			<v-icon color="black" size="25">mdi-github</v-icon>
		</v-btn>

		<v-btn
			class="pl-0 ml-1 mr-4"
			elevation="0"
			min-width="0"
			href="mailto:rustic606@gmail.com"
			target="_blank"
			color="#f8f9fa"			
		>
			<v-icon color="black" size="23">mdi-email-edit</v-icon>
		</v-btn>

		<!-- <v-menu
			bottom
			left
			offset-y
			origin="top right"
			transition="scale-transition"
		>
			<template v-slot:activator="{ attrs, on }">
				<v-btn class="ml-2" min-width="0" text v-bind="attrs" v-on="on">
					<v-badge color="red" overlap bordered>
						<template v-slot:badge>
							<span>4</span>
						</template>

						<v-icon>mdi-bell</v-icon>
					</v-badge>
				</v-btn>
			</template>

			<v-list :tile="false" nav>
				<div>
					<app-bar-item v-for="(n, i) in notifications" :key="`item-${i}`">
						<v-list-item-title v-text="n" />
					</app-bar-item>
				</div>
			</v-list>
		</v-menu> -->
	</v-app-bar>
</template>

<script>
// Components
import { VHover, VListItem } from 'vuetify/lib';

// Utilities
import { mapState, mapMutations } from 'vuex';

export default {
	name: 'DashboardCoreAppBar',

	components: {
		AppBarItem: {
			render(h) {
				return h(VHover, {
					scopedSlots: {
						default: ({ hover }) => {
							return h(
								VListItem,
								{
									attrs: this.$attrs,
									class: {
										'black--text': !hover,
										'white--text secondary elevation-12': hover,
									},
									props: {
										activeClass: '',
										dark: hover,
										link: true,
										...this.$attrs,
									},
								},
								this.$slots.default,
							);
						},
					},
				});
			},
		},
	},

	props: {
		value: {
			type: Boolean,
			default: false,
		},
	},

	data: () => ({
		notifications: [
			'Mike John Responded to your email',
			'You have 5 new tasks',
			"You're now friends with Andrew",
			'Another Notification',
			'Another one',
		],
	}),

	computed: {
		...mapState(['drawer']),
	},

	methods: {
		...mapMutations({
			setDrawer: 'SET_DRAWER',
		}),
	},
};
</script>
